<template>
    <div class="address">
        <mooc-mall-header></mooc-mall-header>
        <mooc-mall-bread>ADDRESS</mooc-mall-bread>
        <div class="step-main">
            <div class="container">
                <ul class="step-ul">
                    <li class="current"><span>Confirm address</span></li>
                    <li><span>View your order</span></li>
                    <li><span>Make payment</span></li>
                    <li><span>Order confirmation</span></li>
                </ul>
                <div class="shipp-address">
                    <h2>SHIPPING ADDRESS
                        <Button @click="addAddressModalVisible=true" icon="ios-add-circle-outline"
                                style="margin-left: 10px;" size="large">
                        </Button>
                    </h2>
                    <ul class="shipp-address-ul">
                        <li @click="changeAddress(address)" class="address-li"
                            :class="{'activeDefaultAddress': defaultAddressChecked==address}"
                            v-for="(address, index) in addresslist" :key="index"
                            v-if="showAddressSplit(index)">
                            <dl>
                                <dt>{{address.username}}</dt>
                                <dd class="address">{{address.postcode+address.streetname}}</dd>
                                <dd class="tel">{{address.tel}}</dd>
                            </dl>
                            <div v-show="address.isDefault" class="checked-address-default">Default Address</div>
                            <div v-show="!address.isDefault" class="checked-address-setDefault">
                                <span><Button @click="setDefaultAddress(address._id)" size="large">Set Default</Button></span>
                            </div>
                            <div @click="delAddressConfirm(address._id)" class="del-address-icon">
                                    <svg class="icon icon-delete">
                                        aria-hidden="true">
                                        <use xlink:href="#icondelete"></use>
                                    </svg>
                            </div>
                        </li>
                    </ul>
                    <div @click="showMoreBtn" v-if="moreBtnVisible" class="adress-more-btn">
                        <span>more</span>
                        <Icon v-if="!moreBtnDownVisible" size="18" type="ios-arrow-down"/>
                        <Icon v-else size="18" type="ios-arrow-up"/>
                    </div>
                    <Modal
                        v-model="addAddressModalVisible"
                        title=""
                        class-name="address-modal"
                        @on-visible-change="loadSwitch"
                        :mask-closable="false"
                        :footer-hide="true">
                        <div class="input-form">
                            <Form ref="formItem" :model="formItem" :rules="ruleValidate" :label-width="85">
                                <FormItem label="" prop="userName">
                                    <Input v-model="formItem.userName" placeholder="..."></Input>
                                </FormItem>
                                <FormItem label="()" prop="postCode">
                                    <Input v-model="formItem.postCode" placeholder="..."></Input>
                                </FormItem>
                                <FormItem label="" prop="ShengShiqu">
                                    <Cascader v-model="formItem.ShengShiqu" :clearable="false" placeholder=""
                                              @on-change="handleChange"
                                              :data="data" trigger="hover"></Cascader>
                                </FormItem>
                                <FormItem label="" prop="localAddress">
                                    <Input v-model="formItem.localAddress" placeholder="..."></Input>
                                </FormItem>
                                <FormItem label="" prop="phoneNumber">
                                    <Input v-model="formItem.phoneNumber" placeholder="..."></Input>
                                </FormItem>
                                <FormItem label="">
                                    <Tooltip v-if="formItem.disabled" content="">
                                        <i-switch v-model="formItem.switch" :disabled="formItem.disabled"
                                                  size="large">
                                            <span slot="open">On</span>
                                            <span slot="close">Off</span>
                                        </i-switch>
                                    </Tooltip>
                                    <i-switch v-if="!formItem.disabled" v-model="formItem.switch"
                                              :disabled="formItem.disabled"
                                              size="large">
                                        <span slot="open">On</span>
                                        <span slot="close">Off</span>
                                    </i-switch>
                                </FormItem>
                                <FormItem>
                                    <Button size="large" type="primary" @click="ok('formItem')"></Button>
                                    <Button size="large" @click="handleReset('formItem')"
                                            style="margin-left: 8px" type="primary">
                                    </Button>
                                </FormItem>
                            </Form>
                        </div>
                    </Modal>
                </div>
                <div class="next-btn-wrapper">
                    <span class="btn btn--red" @click="nextOrderConfirm">NEXT</span>
                </div>
            </div>
        </div>
        <mooc-mall-footer></mooc-mall-footer>
    </div>
</template>

<script>
    import axios from 'axios'
    // 
    import { regionData } from 'element-china-area-data'
    import MoocMallHeader from '@/components/MoocMallHeader'
    import MoocMallBread from '@/components/MoocMallBread'
    import MoocMallFooter from '@/components/MoocMallFooter'

    export default {
        name: 'Address',
        components: {
            MoocMallHeader,
            MoocMallBread,
            MoocMallFooter
        },
        data () {
            // 
            const validateUserName = (rule, value, callback) => {
                if (value == '') {
                    callback(new Error(''))
                } else if (/^[\u4e00-\u9fa5]+$/.exec(value)) {
                    if (value.length > 4) {
                        callback(new Error('4'))
                    } else if (value.length <= 4) {
                        callback()
                    }
                } else if (value.length > 15) {
                    callback(new Error('15'))
                } else if (value.length <= 15) {
                    callback()
                }
            }
            // 
            const validatePostCode = (rule, value, callback) => {
                // console.log(`: ${value}`)
                if (value == '') {
                    callback()
                } else {
                    if (!/^[1-9][0-9]{5}$/.exec(value)) {
                        callback(new Error('1'))
                    } else {
                        callback()
                    }
                }
            }
            // 
            const validateShengShiqu = (rule, value, callback) => {
                if (value.length == 0) {
                    callback(new Error(''))
                } else {
                    // console.log(value.length)
                    callback()
                }
            }
            // (50)
            const validateLocalAddress = (rule, value, callback) => {
                if (value == '') {
                    callback(new Error(''))
                } else if (value.length > 15 || value.length < 5) {
                    callback(new Error('5-15'))
                } else {
                    callback()
                }
            }
            // 
            const validatePhoneNumber = (rule, value, callback) => {
                // console.log(value)
                if (!/^1[0-9]{10}$/.exec(value)) {
                    callback(new Error(''))
                } else {
                    callback()
                }
            }
            return {
                addresslist: [],
                addAddressModalVisible: false,
                formItem: {
                    userName: '',
                    postCode: '',
                    ShengShiqu: [],
                    selectedData: '',
                    phoneNumber: '',
                    switch: true,
                    disabled: false,
                    localAddress: ''
                },
                ruleValidate: {
                    userName: [
                        { required: true, validator: validateUserName, trigger: 'blur' }
                    ],
                    postCode: [
                        { validator: validatePostCode, trigger: 'blur' }
                    ],
                    ShengShiqu: [
                        { required: true, validator: validateShengShiqu, trigger: 'blur' }
                    ],
                    localAddress: [
                        { required: true, validator: validateLocalAddress, trigger: 'blur' }
                    ],
                    phoneNumber: [
                        { required: true, validator: validatePhoneNumber, trigger: 'blur' }
                    ]
                },
                // options: regionData,
                data: regionData,
                moreBtnListNumber: 3,
                moreBtnDownVisible: false,
                setDefaultAddressloading: false,
                defaultAddressChecked: {},
                // selectedOptions: []
            }
        },
        created () {
            this.initAddressList()
        },
        computed: {
            moreBtnVisible () {
                return this.addresslist.length > 4
                // return false
            },
        },
        methods: {
            // 
            nextOrderConfirm () {
                let selectedAddress = this.defaultAddressChecked._id
                this.$router.push({
                    path: `/orderConfirm/${selectedAddress}`
                })
            },
            changeAddress (address) {
                this.defaultAddressChecked = address
            },
            // 
            loadSwitch (flag) {
                if (flag) {
                    axios.get('/users/loadSwitch').then(response => {
                        let res = response.data
                        if (res.status == '0') {
                            this.formItem.disabled = true
                            this.formItem.switch = false
                        } else if (res.status == '00') {
                            this.formItem.disabled = false
                        }
                    })
                }
            },
            // 
            setDefaultAddress (addressId) {
                axios.post('/users/setDefaultAddress', {
                    addressId: addressId
                }).then(response => {
                    let res = response.data
                    if (res.status == '0') {
                        this.initAddressList()
                        this.$Message.success('')
                    }
                })
            },
            showAddressSplit (index) {
                return index <= this.moreBtnListNumber
            },
            showMoreBtn () {
                this.moreBtnDownVisible = !this.moreBtnDownVisible
                if (this.moreBtnDownVisible) {
                    this.moreBtnListNumber = this.addresslist.length
                } else {
                    this.moreBtnListNumber = 3
                }
            },
            ok (name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        axios.post('/users/addAddress', {
                            userName: this.formItem.userName,
                            streetName: this.formItem.selectedData + this.formItem.localAddress,
                            postCode: this.formItem.postCode == '' ? '000000' : this.formItem.postCode,
                            phoneNumber: this.formItem.phoneNumber,
                            isDefault: this.formItem.switch
                        }).then(response => {
                            let res = response.data
                            if (res.status == '0') {
                                this.initAddressList()
                                this.addAddressModalVisible = false
                                this.$Message.success('')
                            } else if (res.status == '12') {
                                this.$Message.info('')
                            }
                        })
                    } else {
                        // this.$Message.error('')
                    }
                })
            },
            handleReset (name) {
                this.$refs[name].resetFields()
            },
            delAddressConfirm (addressId) {
                let v = this
                // console.log(v.addresslist.length)
                if (v.addresslist.length===1) {
                    v.$Modal.warning({
                        title: '',
                        content: ',',
                        closable: true,
                    })
                }else{
                    v.$Modal.info({
                        title: '',
                        content: ',?',
                        closable: true,
                        onOk: function () {
                            axios.post('/users/delAddressItem', {
                                addressId: addressId
                            }).then((response) => {
                                let res = response.data
                                if (res.status == '0') {
                                    v.initAddressList()
                                    v.$message({
                                        type: 'success',
                                        message: '!'
                                    })
                                }
                            })
                        }
                    })
                }

            /* this.$confirm(`, ?`, '', {
                 confirmButtonText: '',
                 cancelButtonText: '',
                 type: 'warning'
             }).then(() => {
                 axios.post('/users/delAddressItem', {
                     addressId: addressId
                 }).then((response) => {
                     let res = response.data
                     if (res.status == '0') {
                         this.initAddressList()
                         this.$message({
                             type: 'success',
                             message: '!'
                         })
                     }
                 })

             }).catch(() => {

             })*/
        },
        handleChange (value, selectedData) {
            // console.log(`${value}`)
            selectedData.forEach(item => {
                if (item.__label) {
                    // this.formItem.selectedData = item.__label
                    this.formItem.selectedData = item.__label.split(' / ').join('')
                    // console.log(this.formItem.selectedData)
                }
            })
        },
        initAddressList () {
            axios.get('/users/addresslist').then((response) => {
                let res = response.data
                if (res.status == '0') {
                    this.addresslist = res.result.addresslist
                    this.addresslist.forEach(item => {
                        if (item.isDefault) {
                            this.defaultAddressChecked = item
                        }
                    })
                }
            })
        }
    }
    }
</script>

<style lang="scss" scoped>
    .address {
        .step-main {
            margin-top: 20px;

            .container {
                .step-ul {
                    /*border: 1px solid #d1434a;*/
                    display: flex;
                    flex-wrap: nowrap;
                    justify-content: space-between;

                    li {
                        line-height: 55px;
                        text-align: center;
                        border-bottom: 3px solid #ccc;
                        width: 25%;
                        position: relative;
                        text-transform: capitalize;

                        &::after {
                            width: 14px;
                            height: 14px;
                            content: '';
                            -webkit-border-radius: 50%;
                            -moz-border-radius: 50%;
                            border-radius: 50%;
                            position: absolute;
                            bottom: -7px;
                            left: 50%;
                            margin-left: -7px;
                            background-color: #ccc;
                        }

                        span {
                            font-weight: 700;
                            font-size: 16px;
                            color: #ccc;
                            /*white-space: nowrap;*/
                        }
                    }

                    .current {
                        border-color: #d1434a;

                        span {
                            color: #d1434a;
                        }

                        &::after {
                            background-color: #d1434a;
                        }
                    }
                }

                .shipp-address {
                    margin-top: 35px;

                    h2 {
                        font-size: 16px;
                        font-weight: 700;
                        letter-spacing: 3px;
                    }

                    .shipp-address-ul {
                        margin-top: 15px;
                        display: flex;
                        justify-content: flex-start;
                        flex-wrap: wrap;

                        .address-li {
                            width: 23.8095%;
                            /*height: 162px;*/
                            border: 2px solid #e9e9e9;
                            background-color: #fff;
                            overflow: hidden;
                            padding: 20px 20px 40px;
                            font-size: 14px;
                            position: relative;
                            margin: 0 0.5% 0.5% 0;
                            -webkit-transition: all .3s ease-in-out;
                            -moz-transition: all .3s ease-in-out;
                            -ms-transition: all .3s ease-in-out;
                            -o-transition: all .3s ease-in-out;
                            transition: all .3s ease-in-out;
                            cursor: pointer;

                            &:hover {
                                border-color: #ee7a23;
                            }

                            dl {
                                dt {
                                    font-weight: 600;
                                    font-size: 17px;
                                }

                                .address {
                                    margin-top: 8px;
                                    color: #515A6E;
                                }

                                .tel {
                                    margin-top: 25px;
                                }
                            }

                            .checked-address-default {
                                color: #ee7a23;
                                margin-top: 5px;
                                font-weight: 600;
                            }

                            .checked-address-setDefault {
                                margin-top: 10px;
                            }

                            .del-address-icon {
                                position: absolute;
                                right: 15px;
                                bottom: 8px;
                                font-size: 24px;

                                .icon-delete {
                                    fill: #605f5f;
                                    -webkit-transition: all .3s ease-in-out;
                                    -moz-transition: all .3s ease-in-out;
                                    -ms-transition: all .3s ease-in-out;
                                    -o-transition: all .3s ease-in-out;
                                    transition: all .3s ease-in-out;
                                    cursor: pointer;

                                    &:hover {
                                        fill: #ee7a23;
                                    }
                                }
                            }
                        }

                        .activeDefaultAddress {
                            border-color: #ee7a23;
                            border-width: 2px;
                        }
                    }

                    .adress-more-btn {
                        -webkit-transition: all .3s ease-in-out;
                        -moz-transition: all .3s ease-in-out;
                        -ms-transition: all .3s ease-in-out;
                        -o-transition: all .3s ease-in-out;
                        transition: all .3s ease-in-out;
                        cursor: pointer;
                        text-align: center;
                        width: 80px;
                        margin: 10px auto 0;
                        color: #ee7a23;

                        span {
                            font-size: 14px;
                            vertical-align: middle;
                        }
                    }
                }

                .next-btn-wrapper {
                    margin-top: 40px;
                    text-align: right;
                }
            }
        }
    }
</style>
